.alert-line {
  cursor: pointer;
  margin-bottom: 10px;
  padding: 10px 20px;
  font-size: 1em;
  border: none;
  border-radius: 5px;
}

.alerts-container {
  padding: 20px;
  text-align: center;
}

.alerts-container h1 {
  font-size: 2em;
  margin-bottom: 20px;
}

.print-btn {
  padding: 10px 20px;
  font-size: 1em;
  margin-bottom: 20px;
  background: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.print-btn:hover {
  background: #0056b3;
}

.alerts-table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
}

.alerts-table th,
.alerts-table td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

.alerts-table th {
  background-color: #333;
  color: white;
}

.load-more-btn {
  padding: 10px 20px;
  font-size: 1em;
  background: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.load-more-btn:hover {
  background: #0056b3;
}
